import { Component, OnInit } from "@angular/core";
import { BaseComponent } from "src/app/components/base.component";
import { engineeringConverterViewModel } from "src/app/models/view/engineering-converter.viewmodel";
import { AllConverters } from "src/app/models/internal/common-models";
import { CommonService } from "src/app/services/common.service";

import { LogHandlerService } from "src/app/services/log-handler.service";
import { TopNavService } from "src/app/services/top-nav.service";
import { TopNavViewModel } from "src/app/models/view/top-nav.viewmodel";
import { commonConverterViewModel } from "src/app/models/view/common-converter.viewmodel";
import { othersViewModel } from "src/app/models/view/others.viewmodel";
import { BankingViewModel } from "src/app/models/view/banking.viewmodel";
import { HealthViewModel } from "src/app/models/view/health.viewmodel";

@Component({
  selector: "app-engineering-converters",
  templateUrl: "./engineering-converters.component.html",
  styleUrls: ["./engineering-converters.component.scss"],
})
export class EngineeringConvertersComponent
  extends BaseComponent<engineeringConverterViewModel>
  implements OnInit
{
  constructor(
    commonService: CommonService,
    logService: LogHandlerService,

    private topNavService: TopNavService
  ) {
    super(commonService, logService);
    this.engineeringConverterViewModel.filteredEngineeringConverters =
      this.allConverters.engineeringConverters;
  }
  allConverters: AllConverters = new AllConverters();
  engineeringConverterViewModel: engineeringConverterViewModel =
    new engineeringConverterViewModel();
  commonConverterViewModel: commonConverterViewModel =
    new commonConverterViewModel();
  topNavViewModel: TopNavViewModel = new TopNavViewModel();
  othersViewModel: othersViewModel = new othersViewModel();
  bankingViewModel: BankingViewModel = new BankingViewModel();
  healthViewModel: HealthViewModel = new HealthViewModel();
  ngOnInit() {
    this.engineeringConverterViewModel.isPower = true;
    this.topNavService.sharedDataSubject.subscribe((data) => {
      this.topNavViewModel.receivedSearchValue = data;
      if (this.topNavViewModel.receivedSearchValue.trim() != "") {
        this.engineeringConverterViewModel.filteredEngineeringConverters = [
          ...this.allConverters.commonConverters,
          ...this.allConverters.engineeringConverters,
          ...this.allConverters.otherConverters,
          ...this.allConverters.bankingConverters,
          ...this.allConverters.healthConverters,
        ].filter((item) => {
          this.engineeringConverterViewModel.isForce = false;
          this.engineeringConverterViewModel.isWork = false;
          this.engineeringConverterViewModel.isPower = false;
          this.engineeringConverterViewModel.isPlaneAngle = false;
          this.engineeringConverterViewModel.isPressure = false;
          this.engineeringConverterViewModel.isCurrent = false;

          return item.searchArrayFromConverters.some((term) =>
            term
              .toLowerCase()
              .includes(this.topNavViewModel.receivedSearchValue.toLowerCase())
          );
        });
      } else {
        this.engineeringConverterViewModel.filteredEngineeringConverters =
          this.allConverters.engineeringConverters;

        this.engineeringConverterViewModel.isForce = false;
        this.engineeringConverterViewModel.isWork = false;
        this.engineeringConverterViewModel.isPower = true;
        this.engineeringConverterViewModel.isPlaneAngle = false;
        this.engineeringConverterViewModel.isPressure = false;
        this.engineeringConverterViewModel.isCurrent = false;
        this.commonConverterViewModel.isWeight = false;
        this.commonConverterViewModel.isSpeed = false;
        this.commonConverterViewModel.isLength = false;
        this.commonConverterViewModel.isTemperature = false;
        this.commonConverterViewModel.isVolume = false;
        this.commonConverterViewModel.isArea = false;
        this.commonConverterViewModel.isTime = false;
        this.commonConverterViewModel.isFrequency = false;
        this.commonConverterViewModel.isCurrency = false;
        this.othersViewModel.isDob = false;
        this.othersViewModel.isStorage = false;
        this.othersViewModel.isCooking = false;
        this.othersViewModel.isFuel = false;
        this.othersViewModel.isTimezone = false;
        this.bankingViewModel.isCompoundInterest = false;
        this.bankingViewModel.isDiscount = false;
        this.bankingViewModel.isSimpleInterest = false;
        // health
        this.healthViewModel.isBmi = false;
        this.healthViewModel.isBmr = false;
        this.healthViewModel.isWaterIntake = false;
      }
    });

    // search ends
    this.engineeringConverterViewModel.localStorageItems =
      this._commonService.getAllItemsFromLocalStorage();
    for (const item of [
      ...this.allConverters.commonConverters,
      ...this.allConverters.engineeringConverters,
      ...this.allConverters.otherConverters,
      ...this.allConverters.bankingConverters,
      ...this.allConverters.healthConverters,
    ]) {
      item.isFavorite =
        this.engineeringConverterViewModel.localStorageItems.some(
          (favoriteItem) => favoriteItem.title === item.title
        );
    }

    // Find and mark the default selected item
    const defaultSelectedItemTitle = "Power"; // Replace with the actual title

    this.engineeringConverterViewModel.defaultSelectedItem =
      this.engineeringConverterViewModel.filteredEngineeringConverters.find(
        (item) => item.title === defaultSelectedItemTitle
      );

    if (this.engineeringConverterViewModel.defaultSelectedItem) {
      this.engineeringConverterViewModel.defaultSelectedItem.isSelected = true;
    }
  }

  showCalculators(calculator: string) {
    // Common converters
    const common = this.commonConverterViewModel;
    const engineering = this.engineeringConverterViewModel;
    const others = this.othersViewModel;
    // Reset all calculator flags to false
    common.isWeight =
      common.isSpeed =
      common.isArea =
      common.isTemperature =
      common.isCurrency =
        false;
    common.isLength =
      common.isVolume =
      common.isTime =
      common.isFrequency =
        false;

    engineering.isPressure = engineering.isWork = engineering.isForce = false;
    engineering.isCurrent =
      engineering.isPower =
      engineering.isPlaneAngle =
        false;

    others.isCooking =
      others.isStorage =
      others.isFuel =
      others.isDob =
      others.isTimezone =
        false;

    this.bankingViewModel.isCompoundInterest = false;
    this.bankingViewModel.isDiscount = false;
    this.bankingViewModel.isSimpleInterest = false;

    // health
    this.healthViewModel.isBmi = false;
    this.healthViewModel.isBmr = false;
    this.healthViewModel.isWaterIntake = false;
    // Set the selected calculator to true
    if (calculator === "Weight") {
      common.isWeight = true;
    } else if (calculator === "Currency") {
      common.isCurrency = true;
    } else if (calculator === "Speed") {
      common.isSpeed = true;
    } else if (calculator === "Time") {
      common.isTime = true;
    } else if (calculator === "Area") {
      common.isArea = true;
    } else if (calculator === "Length") {
      common.isLength = true;
    } else if (calculator === "Temprature") {
      common.isTemperature = true;
    } else if (calculator === "Volume") {
      common.isVolume = true;
    } else if (calculator === "Force") {
      engineering.isForce = true;
    } else if (calculator === "Pressure") {
      engineering.isPressure = true;
    } else if (calculator === "Work/Energy") {
      engineering.isWork = true;
    } else if (calculator === "Power") {
      engineering.isPower = true;
    } else if (calculator === "Current") {
      engineering.isCurrent = true;
    } else if (calculator === "Plane Angle") {
      engineering.isPlaneAngle = true;
    } else if (calculator === "Frequency") {
      common.isFrequency = true;
    } else if (calculator === "Cooking") {
      this.othersViewModel.isCooking = true;
    } else if (calculator === "Computer Data") {
      this.othersViewModel.isStorage = true;
    } else if (calculator === "Fuel Efficiency") {
      this.othersViewModel.isFuel = true;
    } else if (calculator === "Age Calculator") {
      this.othersViewModel.isDob = true;
    } else if (calculator === "World Clock") {
      this.othersViewModel.isTimezone = true;
    } else if (calculator === "Compound Interest") {
      this.bankingViewModel.isCompoundInterest = true;
    } else if (calculator === "Simple Interest") {
      this.bankingViewModel.isSimpleInterest = true;
    } else if (calculator === "Discount") {
      this.bankingViewModel.isDiscount = true;
    } else if (calculator === "BMI Calculator") {
      this.healthViewModel.isBmi = true;
    } else if (calculator === "BMR Calculator") {
      this.healthViewModel.isBmr = true;
    } else if (calculator === "Water Intake") {
      this.healthViewModel.isWaterIntake = true;
    }

    // Update selected item
    if (engineering.selectedItem) {
      engineering.selectedItem.isSelected = false;
    }
    engineering.selectedItem = engineering.filteredEngineeringConverters.find(
      (x) => x.title === calculator
    );
    if (engineering.selectedItem) {
      engineering.selectedItem.isSelected = true;
      engineering.defaultSelectedItem.isSelected = false;
    }
  }
}
